.cart-page {
    margin-top: 64px;
  }
  
  .cart-page h1 {
    padding: 15px !important;
    font-family: "roboto", sans-serif;
    font-weight: normal;
    background-color: rgba(0, 0, 255, 0.072) !important;
  }
  .cart-page h1 > p {
    font-size: 20px;
    margin-top: 10px;
  }
  
  .cart-page .card {
    padding: 5px !important;
    height: 180px !important;
    margin-bottom: 5px !important;
  }
  .cart-page .cart-remove-btn {
    display: flex;
    align-items: center;
    justify-content: center;
  }
  .cart-page .cart-summary {
    text-align: center;
  }

  .cart_product2, .cart_product3_name{
    cursor: pointer;
  }
  .cart_product2_img:hover{
    scale: .9;}

  @media screen and (max-width: 770px) {
    .cart_product2_img{
      width: auto;
      height: 140px;}

      .cart_product2{
        display: flex;
        justify-content: center;
      }

    
       .cart_product3 {
     display: flex;
     flex-direction: column;

    }
    .cart-page .card {
      height: 300px !important;
    }
  }