@import url('https://fonts.googleapis.com/css2?family=Playfair+Display:wght@700&family=Poppins:wght@300;400&display=swap');

*{
  margin: 0;
  padding: 0;
  box-sizing: border-box;
  
}

/*
 font-family: 'Poppins', sans-serif; 
  font-family: 'Playfair Display', serif;
*/

/* navbar */
.navbar {
  
  font-family: 'Poppins', sans-serif; 
  font-size: 17px;
  line-height: 26px;
  text-transform: uppercase;
  box-shadow: 0 8px 6px -6px gray;
  --webkit-box-shadow: 0 8px 6px -6px gray;
  border-bottom: 0px solid gray !important;
  border-radius: 20px;
  box-shadow: #434343;
  padding: 6px 4px;
}

.nav-link {
  font-weight: 300 !important;
}

.nav-link:hover {
  border-bottom: 1px solid rgb(97, 91, 91);
}

.active{
  /* border-bottom: 1px solid rgb(97, 91, 91); */
  
}

.navbar-brand {
  font-weight: 700;
  font-family: 'roboto', sans-serif;
  letter-spacing: 3px;
  
}

.navbar-brand img {
  width: 40px;
  margin-right: 15px;
  border-radius: 20px;
}

/* footer ----------------------------------------------------------------*/
footer {
  color:'white';
  padding: 25px;
  background: rgb(29, 28, 28);
}

.footer a {
  text-decoration: none;
  color: white;
  padding: 10px;
}

.footer a:hover{
  color: #ffefba;
  border-bottom: 1px solid #ffefba;
}

.backtop {
  
  width: 100%;
  height: 50px;
  background-color: rgb(39, 43, 48);
  padding: 0px 10px;
  cursor: pointer;
  border-bottom: transparent;
  margin-bottom: 10px;
  margin-top: 1px;
  
}
.backtop_p{
  padding-top: 15px;
}

.backtop:hover {
 
  background-color: rgb(53, 52, 52);
 
}
/* page not found */
.pnf {
  display: flex;
  min-height: 65vh;
  flex-direction: column;
  align-items: center;
  justify-content: center;
}

.pnf-heading {
  font-weight: normal;
}

.pnf-btn {
  color:black;
  border: 1px  solid black;
  text-decoration: none;
  padding: 10px;
  margin-top: 10px;
}

/* =========contact us ========= */
.contactus {
  margin: 0;
  padding: 0;
  height: 70vh;
  display: flex;
  align-items: center;
  justify-content: center;
}

/* ==================== */
.product-link {
  text-decoration: none !important;
  color: black !important;
}

.cat-btn {
  padding: 40px 0px;
  font-size: 24px;
  text-transform: uppercase;
}
.cat-btn:hover {
  background-color: #434343;
  color: white;
}



/* register-------------------------------- */
.register {
  display: flex;
  align-items: center;
  justify-content: center;
  height: 70vh;
  flex-direction: column;
}


/* ================
====dashboard
=================== */

.dashboard {
  margin-top: 100px !important;
}

.dashboard-menu h4 {
  background-color: #434343 !important;
  color: white;
  padding: 20px 0px;
}



/* Homepage---------------- */

.main-container {
  
  margin-top: -80px;
  
  position: relative;
  display: flex;
  flex-direction: column;
  margin-bottom: 10px;
}

.card-product{
  background-color: rgb(255, 255, 255);
  display: flex;
  flex-direction: column;
  justify-content: space-between;
  max-width: 310px;
  box-shadow: rgba(0, 0, 0, 0.35) 0px 5px 15px;
  padding: 10px;
  
}

.card-product-sign {
  background-color: white;
  display: flex;
  flex-direction: column;
  justify-content: space-between;
  max-width: 310px;
  height: 140px;
  box-shadow: rgba(0, 0, 0, 0.35) 0px 5px 15px;
  padding: 10px;
}

.card-nested-banner img {
  width: 310px;
  height: 285px;
  object-fit: cover;
  margin-top: 12px;
  margin-left: 0px;
  padding-top: 45px;
}

.card-nested-sign-in {
  margin-top: 10px;
}
.sign-in {
  background-color: rgb(255, 251, 45);
  color: black;
  padding: 8px 90px;
  border: none;
  border-radius: 10px;
  border: 1px solid rgb(255, 255, 79);
  font-family: Arial,sans-serif;
  
}

.card-product h2, .card-product-sign h2{
  margin: 0;
  padding: 10px;
  font-family: Arial,sans-serif;
  color: #0F1111;
  font-size: 21px;
  
  
}

.card-product-nested-card {
  display: flex;
  flex-wrap: wrap;
  justify-content: space-evenly;
  cursor: pointer;


}

.card-nested p{
  
  width: 128px;
  height: 25px;
  
  font-family: Arial,sans-serif;
  font-size: 12px;
  margin-top: 0px;
  
}
.card-nested img {
  width: 128px;
  height: 102px;
  object-fit: cover;
  

} 

.card-nested-single-product img{
  width: 290px;
  height: 280px;
  object-fit: cover;
  padding: 5px;
  
}

.card-product-container{
    padding: 20px;
    display: flex;
    justify-content: space-between;

}

.card-product-btn{
  font-size: 14px;
  align-self: flex-start;
  margin: 7px 10px; 
  padding-left: 0;
  background-color: transparent;
  border:none;
  color:rgb(25, 105, 105)
}
.card-product-btn:hover{
  text-decoration: underline;
  cursor:pointer
}


.poster {
  
  display: flex;
  justify-content: center;
} 

.image-poster:hover {
  
  cursor: pointer;
} 


/* product slider  */

.product_container_slider_1{
  position: relative;
  margin: 20px;
  box-shadow: -2px -2px 5px rgba(0, 0, 0, 0.1),
  2px 2px 5px rgba(0, 0, 0, 0.1);
  
}
.product_container_slider_heading_1 {
  display: flex;
  align-items: center;
  padding: 2px 20px;
}
.product_container_slider_heading_1 h1 {
  font-size: 25px;
}
.product_container_slider_heading_1 p {
  color:rgb(39, 114, 107);
  text-decoration: none;
  padding-left: 10px;
  cursor: pointer;
}
.product_container_slider_heading_1 p:hover{
  text-decoration: underline;
}
.product_container_slider_product_container_1{
  height: 230px;
  display: flex;
  align-items: center;
  cursor: pointer; 
  overflow: auto;
  scroll-behavior: smooth;
}

/* scrollbar */

::-webkit-scrollbar{
  height: 10px;
  width: 6px;
  background: rgb(199, 194, 194);
}
::-webkit-scrollbar-track {
background: #f1f0f0; 
}
::-webkit-scrollbar-thumb {
background: #888; 
}
::-webkit-scrollbar-thumb:hover {
background: #c2bebe; 
} 
::-webkit-scrollbar-thumb:horizontal{
  background: #c2bdbd;
  border-radius: 20px;
}


.product_container_slider_list_1{
  display: flex;
}
.product_container_slider_list_1 .product_container_slider_item_1{
  height: 200px;
  padding: 0px 2.5px;
  margin-right: 10px;
}

.product_container_slider_image_1{
  display: flex;
  justify-content: center;
  align-content: center;
  margin-bottom: 15px;
  
}
.product_container_slider_item_1 img{
  height: 100%;
}

.product_container_slider_item_1 img:hover{
  scale: 1.01;
}

.product_container_slider_btn_container_1{
  position: absolute;
  width: 100%;
  display: flex;
  justify-content: space-between;
  align-items: center;
  height: 100%;
  padding: 20px;
  z-index: 10;
  
}

.product_container_slider_btn_1, .product_container_slider_prev_1, .product_container_slider_next_1 {
  padding: 15px 5px;
  font-size: 40px;
  border: none;
  outline: none;
  background-color: rgb(255,255,255,0.4);
  cursor: pointer;
  opacity: .7;
  
  
}

.product_container_slider_btn_1:hover, .product_container_slider_prev_1:hover,  .product_container_slider_next_1:hover{
  background-color: rgba(238, 232, 232, 0.4);
  opacity: 1;
}

.product_container_slider_imgcard{
  display: flex;

}
.card__container--inner{
  display: flex;
}

.slick-next,
.slick-prev {
  padding: 20px 20px;
    width: 2vw;
    height: 2vw;
    cursor: pointer;
    border: none;
    color:#0F1111;
    
    background-color: #2b2828;
    z-index: 99 !important;
    box-shadow: rgb(35 35 35 / 50%) 0px 0px 20px 5px;
    top: 40%;
    &:hover{
    color:#1b1919;

    }
}


.left1{
  float: left; 
  width: 3%;
  height: 200px;
  align-items: center;
 }


 
 .internal1{
  height: 100%;
  display: inline-block;
  padding: 0 5px;
 }
 
 .center1{
  float: left; 
  width: 93.5%;
  height: 220px;
  margin: 1px;
  overflow: auto;
  white-space: nowrap;
  scroll-behavior: smooth;
 }
 
 .right1{
  float: right; 
  width: 3%;
  height: 200px;
  border:  1px white solid;
 }
 
 .left11 {
  width: 100%;
  height: 100%;
  font-size: 30px;
 }

 .left11:hover {

 background-color: #d6c6c6;
 }

 .ps_main {
  float: left; 
  width: 99%;
  height: 280px;
  box-shadow: -2px -2px 5px rgba(0, 0, 0, 0.1),
  2px 2px 5px rgba(0, 0, 0, 0.1);
  margin: 5px;
  overflow: hidden;
  cursor: pointer;
 }
 .internal1{
  height: 200px;
 }

 .ps_item_1:hover{
  cursor: pointer; 
  scale: 1.01;
 }

 .ps_heading_1 {
  display: flex;
  align-items: center;
  padding: 2px 20px;
}
 .ps_heading_1 h1 {
  font-size: 25px;
}
 .ps_heading_1 p {
  color:rgb(39, 114, 107);
  text-decoration: none;
  padding-left: 10px;
  cursor: pointer;
}
 .ps_heading_1 p:hover{
  text-decoration: underline;
}
 


.main_slider_6{
  width: 100%;
  height: 230px;
  position: relative;
  display: flex;
  align-items: center;
  white-space: nowrap;
}


.left6{
    
    position: absolute;
    cursor: pointer;
    z-index: 10;
  height: 100px;
  align-items: center;
  opacity: .5;
  border: #c2bdbd;
}
.right6{
  cursor: pointer;
  z-index: 10;
  position: absolute;
height: 100px;
opacity: .5;

}

.left6.left{
  left: 0;
}

.right6.right{
  right: 0;
}
.left16 {
  
  width: 100%;
  height: 100%;
  font-size: 30px;
  align-items: center;
 }
 .left6:hover,  .right6:hover {
  opacity: 1;
 }



/* all product */
.filter_btn{
  display: none;
}

@media screen and (max-width: 1300px) {

  .image-poster {
    width: 80%;
  } 

  .card-product-container{
    display: flex;
    flex-direction: column;
  }
  .card-product{
    max-width: 100%;
    padding: 5px 0px;
    box-shadow: rgba(0, 0, 0, 0.35) 0px 5px 15px;
  }
  .product{
    display: none;
  }
  .card-product-nested-card, {
    display: flex;
    flex-direction: row;
  }
  .card-nested img {
    width: 165px;
    height: 150px;
    object-fit: cover;
    
  
  } 
}

@media screen and (max-width: 720px) {
  .main-container {
    margin-top: -55px;
  }

  .card-product-container{
    display: flex;
    flex-direction: column;
    margin-left: auto;
    margin-right: auto;
  }
  .image-poster {
  
    width: 85%;
  } 
  .product{
    display: block;
  }
  .card-product {
    max-width: 350px;
    margin-bottom: 10px;
    
    
  }
  .card-nested img {
    width: 128px;
    height: 102px;
    object-fit: cover;
    
  
  } 
  .product_container_slider_heading_1 h1 {
    font-size: 15px;
  }
   .product_container_slider_heading_1 p {
    /* display: none; */
    font-size: 10px;
  }
  .product_container_slider_heading_1{
    width: 100%;
  }
  .left1,.right1{
    display: none;
  }
  .ps_main,{
    height: 300px;
  }

  .navbar-brand{
    margin-right: 45px;
  }

  .allproduct_mobile{
    margin-left: 20px;
  }

  .filter_btn{
    display: block;
    margin-left: 10px;
    
  }
  .filter1{
    display: none;
    
  }
  .product{
    margin-bottom: 220px;
    margin-left: 10px;
  }

  .left6{
    display: none;
}
.right6{
  display: none;

}
 
  

  
}

