/* ============================================ register page  */
.form-container {
    margin-top: 0px;
    display: flex;
    align-items: center;
    justify-content: center;
    min-height: 80vh;
    flex-direction: column;
    background-color: #ffdee9;
    background-image: linear-gradient(0deg, #ffdee9 0%, #b5fffc 100%);
    
  }
  
  .form-container form {
    box-shadow: rgba(0, 0, 0, 0.35) 0px 5px 15px;
    padding: 20px;
    background-color: #fff;
    border-radius: 20px;
  }
  
  .form-container form .title {
    text-align: center;
    margin-bottom: 15px;
    font-weight: bold;
    font-family: "Playfair Display", serif;
  }
  
  .form-container form input {
    border: none;
    border: 1px solid #8b8585;
    border-radius: 20px;

  }
  
  .form-container form button {
    border: 1px solid #0b793c;
    border-radius: 0;
    background-color: #0b793c;
    color: white;
    width: 250px;
    border-radius: 20px;
  }
  
  .form-container form button:hover {
    border: 1px solid rgb(15, 73, 24);
    background: rgb(15, 73, 24); 
  }
  
  .forgot-btn {
    background-color: #434343a4 !important;
    border: 0 !important;
  }
  
  .forgot-btn:hover {
    color: white !important;
  }